import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {AlertController} from "@ionic/angular";
import {Router} from "@angular/router";
import {API_KEY, API_URI} from "../../../../environments/environment";
import {Notification} from "../../../store/models/notification";
import {TodoAdd} from "../../../store/actions/notification-action";
import {Store} from "@ngrx/store";

@Component({
  selector: 'app-feed-back-app',
  templateUrl: './feed-back-app.component.html',
  styleUrls: ['./feed-back-app.component.scss'],
})
export class FeedBackAppComponent implements OnInit {

  form: FormGroup;
  constructor(private fb: FormBuilder, private http: HttpClient,
              private alertController: AlertController, private router: Router,
              private store : Store<{notification: Notification[]}>) {
    this.createForm();
  }

  ngOnInit() {}

  addNotification(currentRoom ){
    const newNotification = new Notification();
    newNotification.notification = currentRoom;
    this.store.dispatch(new TodoAdd(newNotification))
  }

  createForm() {
    this.form = this.fb.group({
      feed: ['', Validators.required],
      description: ['', Validators.required ]
    });
  }

  async demoAlert(){
    const alert = await this.alertController.create({
      cssClass: 'my-custom-popup',
      subHeader: '',
      message: 'Erreur champs manquants',
      buttons: [
        {
          text: 'Ok',
        }
      ],
    });
    await alert.present();
  }

  sendForm() {
    let notification = {
      message :'La notation que vous avez transmise a bien t prise en compte',
      service:'Enqutes de satisfaction',
    }
    if(!this.form.valid) {
      this.demoAlert();
    } else {
      const header = new HttpHeaders({
        'Content-Type': 'application/json',
        'Authorization': API_KEY });
      const options = { headers: header };
      const url = API_URI + '/iviplay/feed-back/app';
      this.http.post(url, this.form.value, options).subscribe(data => {
        if(data) {
          this.addNotification(notification);
          this.messageAlert();
          this.router.navigate(['/tabs/tab2']);
        }
      });
    }
  }

  async messageAlert(){
    const alert = await this.alertController.create({
      cssClass: 'my-custom-popup',
      subHeader: '',
      message: 'La notation que vous avez transmise a bien t prise en compte',
      buttons: [
        {
          text: 'Ok',
        }
      ],
    });
    await alert.present();
  }

}
